
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: { Loading, Toast },
data() {
return {
yzm_text: "获取验证码",
load_info: {
show: false,
text: "加载中..."
},
toast_info: {
show: false,
time: 1000,
type: "text",
text: "加载中..."
},
select_list: ["医院", "门店", "亲子活动"],
cur_select: 0,
tel_reg: /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/
};
},
methods: {
get_yzm() {
var tel_val = this.$refs.tel.value;
if (!tel_val) {
this.get_toast("请输入手机号");
} else if (!this.tel_reg.test(tel_val)) {
this.get_toast("手机号有误");
} else if (this.yzm_text != "获取验证码") {
this.get_toast("已发送验证码 注意查收");
} else {
this.yzm_text = 60;
var timer = setInterval(() => {
if (this.yzm_text > 1) {
this.yzm_text--;
} else {
clearInterval(timer);
this.yzm_text = "获取验证码";
}
}, 1000);
}
},
get_toast(text) {
this.toast_info.show = true;
this.toast_info.type = "warn";
this.toast_info.text = text;
},
change_state() {
this.toast_info.show = false;
}
}
};



